/* Внутренние стили дизайн системы - они не должны включаться в конечную сборку! */

body {
    background: #eef2f4;
    margin: 0;
    padding: 0;
    &::-webkit-scrollbar {
        width: 10px;
        background-color: #dbdbdb;
    }

    &::-webkit-scrollbar-thumb {
        background-color: #318cca;
    }
}

.ds-title {
    width: 100%;
    text-align: center;
    color: #343b4c;
    margin-block-start: 2em;
    margin-block-end: 2em;
}

.ds-wrapper {
    max-width: 1300px;
    margin: 20px auto;
    padding: 20px;
    background-color: white;
    box-shadow: 0 0 8px 1px rgba(0, 0, 0, 0.1);
}

.ds-text {
    max-width: 1300px;
    margin: 20px auto;
}

.ds-code-wrapper {
    max-width: 1300px;
    margin: 40px auto;
    border-radius: 10px;
    box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.5);
}

// обозначение интерактивности элемента
.ds-i {
    position: relative;

    &:before {
        content: "Интерактивный элемент";
        display: block;
        position: absolute;
        background: #36a6f2;
        padding: 10px 12px;
        line-height: 15px;
        color: white;
        border-radius: 6px;
        font-size: 14px;
        font-weight: 600;
        font-style: normal;
        min-width: 120px;
        box-shadow: 0 0 2px 0px rgba(0, 0, 0, 0.1);
        bottom: 50%;
        right: -15px;
        left: auto;
        transform: translate(100%, 50%);
        text-align: center;
    }

    &:after {
        content: "";
        display: block;
        background: #36a6f2;
        width: 10px;
        height: 10px;
        position: absolute;
        transform: translateY(-50%) rotate(45deg);
        right: -21px;
        top: 50%;
    }
}

.ds-divider {
    margin: 20px;
}

.ds-wrapper > svg {
    width: 50px;
    height: 50px;
    margin: 0 20px 20px 0;
}

.ds-сontent-list {
    li {
        margin-bottom: 5px;
        font-size: 16px;
    }
}

.ds-hl {
    font-weight: 600;
    background: rgb(255, 229, 85);
    padding: 2px 7px;
}

.no-padding {
    padding: 0;
}

.ds-center {
    display: flex;
    justify-content: center;
    align-items: center;
}

// переопределение кнопки "Copy" в примерах кода
.code-toolbar > .toolbar button {
    color: black !important;
    font-weight: bold !important;
    padding: 0.5em 0.6em !important;
    background: rgb(235, 234, 234) !important;
    cursor: pointer !important;
}

pre[class*="language-"] {
    padding: 2rem !important;
    font-size: 1rem !important;
    background: #171717 !important;
    border-radius: 10px !important;
    max-height: 500px !important;
    &::-webkit-scrollbar {
        width: 5px;
        height: 5px;
        padding: 0 2px;
        background-color: #dbdbdb;
    }

    &::-webkit-scrollbar-thumb {
        background-color: #318cca;
    }
}
